import "./debtCardTabClient.module.scss";

const DebtCardTabClient = ({personData}) => {

    //TODO:      onChangeInput,        .      1     ""      .

    return (
        <div className="debt-card-tab-client">
            <div className="debt-card-tab-client__fields-container">
                <div className="fields__field-container" style={{width: 100}}>
                    <label className="fields__label" htmlFor="clientId">ID</label>
                    <input className="fields__input" defaultValue={personData.personId} type="number" id="clientId" name="clientId" readOnly="readonly"/>
                </div>
                <div className="fields__field-container" style={{width: 120}}>
                    <label className="fields__label" htmlFor="birthDt"> </label>
                    <input className="fields__input" defaultValue={personData.birthDt} type="text" id="birthDt" name="birthDt" readOnly="readonly"/>
                </div>
                <div className="fields__field-container" style={{width: 80}}>
                    <label className="fields__label" htmlFor="age"></label>
                    <input className="fields__input" defaultValue={personData.age} type="text" id="age" name="age" readOnly="readonly"/>
                </div>
                <div className="fields__field-container" style={{width: 100}}>
                    <label className="fields__label" htmlFor="gender"></label>
                    <input className="fields__input" defaultValue={personData.gender} type="text" id="gender" name="gender" readOnly="readonly"/>
                </div>
                <div className="fields__field-container" style={{width: 150}}>
                    <label className="fields__label" htmlFor="identityDoc"> . .</label>
                    <input className="fields__input" defaultValue={personData.identityDoc} type="text" id="identityDoc" name="identityDoc" readOnly="readonly"/>
                </div>
                <div className="fields__field-container" style={{width: 210}}>
                    <label className="fields__label" htmlFor="identityDocNumber">    . .</label>
                    <input className="fields__input" defaultValue={personData.identityDocNumber} type="text" id="identityDocNumber" name="identityDocNumber" readOnly="readonly"/>
                </div>
                <div className="fields__field-container" style={{width: 250}}>
                    <label className="fields__label" htmlFor="birthPlace"> </label>
                    <input className="fields__input" defaultValue={personData.birthPlace} type="text" id="birthPlace" name="birthPlace" readOnly="readonly"/>
                </div>
                <div className="fields__field-container" style={{width: 150}}>
                    <label className="fields__label" htmlFor="INN"></label>
                    <input className="fields__input" defaultValue={personData.INN} type="text" id="INN" name="INN" readOnly="readonly"/>
                </div>
            </div>
        </div>
    )
}

export default DebtCardTabClient;